

const priceAction = document?.querySelectorAll('.card__price-new');
const priceNotAction = document?.querySelectorAll('.card__price-old');
priceAction?.forEach(el => {
  const cardAction = el.closest('.card__box-price');
  const cardActionOldPrice = cardAction.querySelector('.card__price-old');
  cardActionOldPrice.classList.add('old');

})




