
export const calcCart = () => {
  const cartWrapper = document?.querySelector('.shopcart__list');
  const priceElements = cartWrapper?.querySelectorAll('.shopcart__item-price');
  const totalPrice = document?.querySelector('.shopcart__total-sum');
  // Общая стоимость товаров
  let priceTotal = 0;
  // Обходим все блоки с ценами в корзине
  priceElements.forEach(item => {
    // Находим количество товара
    const amount = item.closest('.shopcart__item-quantity').querySelector('[data-counter]');
    // Добавляем стоимость товара в общую стоимость (кол-во * цену)
    priceTotal += parseInt(item.innerText) * parseInt(amount.innerText);
  });
  // Отображаем цену на странице
  totalPrice.innerText = priceTotal;
}


