import { disableScroll } from '../functions/disable-scroll';
import { enableScroll } from '../functions/enable-scroll';

(function(){
  const shop = document?.querySelector('[data-shop]');
  const cart = document?.querySelector('[data-cart]');
  const cartItems = document?.querySelectorAll('[data-cart-item]');
  const overlay = document?.querySelector('[data-cart-overlay]');

  shop?.addEventListener('click', (e) => {
    shop?.classList.toggle('shop--active');
    cart?.classList.toggle('cart--active');

    if (cart?.classList.contains('cart--active')) {
      shop?.setAttribute('aria-expanded', 'true');
      shop?.setAttribute('aria-label', 'Закрыть корзину');
      disableScroll();
    } else {
      shop?.setAttribute('aria-expanded', 'false');
      shop?.setAttribute('aria-label', 'Открыть корзину');
      enableScroll();
    }
  });

  overlay?.addEventListener('click', () => {
    shop?.setAttribute('aria-expanded', 'false');
    shop?.setAttribute('aria-label', 'Открыть корзину');
    shop.classList.remove('shop--active');
    cart.classList.remove('cart--active');
    enableScroll();
  });

  cartItems?.forEach(el => {
    el.addEventListener('click', () => {
      shop?.setAttribute('aria-expanded', 'false');
      shop?.setAttribute('aria-label', 'Открыть корзину');
      shop.classList.remove('shop--active');
      cart.classList.remove('cart--active');
      enableScroll();
    });
  });
})();
