// console.log('tabs');

let listbtn = document.querySelectorAll('.tabs__nav-btn');
let itembox = document.querySelectorAll('.tabs__box');

for (let i = 0; i < listbtn.length; i++) {
  listbtn[i].addEventListener('click', function () {
    for (let j = 0; j < listbtn.length; j++) {
      listbtn[j].classList.remove('active');
    }
    this.classList.add('active');

    let dataFilter = this.getAttribute('data-filter');

    for (let k = 0; k < itembox.length; k++) {
      itembox[k].classList.remove('active');
      itembox[k].classList.add('hide');

      if (itembox[k].getAttribute('data-item') == dataFilter || dataFilter == "all") {
        itembox[k].classList.remove('hide');
        itembox[k].classList.add('active');
      }
    }
  })
}

