
import { cartStatus } from '../components/cartStatus';
import { calcCart } from '../components/calcCart';

// Div внутри корзины, в который мы добавляем товары
const cartWrapper = document.querySelector('.shopcart__list');


// Отслеживаем клик на странице
window.addEventListener('click', function (event) {
  // Проверяем что клик был совершен по кнопке "Добавить в корзину"
  if (event.target.hasAttribute('data-cart')) {
    // Находим карточку с товаром, внутри котрой был совершен клик
    const card = event.target.closest('.card');
    // Собираем данные с этого товара и записываем их в единый объект productInfo
    const priceNew = card.querySelector('.card__price-new')?.innerText;
    const priceOld = card.querySelector('.card__price-old').innerText;
    let price;
    //  Проверяем наличие новой цены
    (priceNew ? price = priceNew : price = priceOld);

    // if (priceNew) {
    //   price = priceNew;
    // } else {
    //   price = priceOld;
    // }


    // console.log(price);

    const productInfo = {
      id: card.dataset.id,
      imgSrc: card.querySelector('.card__img').getAttribute('src'),
      title: card.querySelector('.card__title').innerText,
      price: price,
      itemsInBox: card.querySelector('.card__volume').innerText,
      counter: card.querySelector('[data-counter]').innerText,
    };

    // Проверять если ли уже такой товар в корзине
    const itemInCart = cartWrapper.querySelector(`[data-id="${productInfo.id}"]`);
    // Если товар есть в корзине
    if (itemInCart) {
      const counterElement = itemInCart.querySelector('[data-counter]');
      counterElement.innerText = parseInt(counterElement.innerText) + parseInt(productInfo.counter);
    } else {
      // Если товара нет в корзине
      const cartItemHTML = `<li class="shopcart__item" data-id="${productInfo.id}">
    <img src="img/icons/del.svg" class="shopcart__item-del" width="24" height="24" alt="delete item" data-action="del">
    <div class="shopcart__item-box">
    <p class="shopcart__item-title">${productInfo.title}</p>
      <p class="shopcart__item-quantity">
        <span data-counter>${productInfo.counter}</span>
        <span>×</span>
        <span class="shopcart__item-price">${productInfo.price}</span>
      </p>
    </div>
  </li>`;

      // Отобразим товар в корзине
      cartWrapper.insertAdjacentHTML('beforeend', cartItemHTML);
    }
    // Сбрасываем счетчик добавленного товара на "1"
    card.querySelector('[data-counter]').innerText = '1';

    // Отображение статуса корзины Пустая / Полная
    cartStatus();
    // Пересчет общей стоимости товаров в корзине
    calcCart();
  }

});
