
console.log('рендер');

const productsContainer = document.querySelector('#products-container');
console.log(productsContainer);
// Запускаем getProducts
getProducts();

// Асинхронная функция получения данных из файла products.json
async function getProducts() {
  // Получаем данные из products.json
  const response = await fetch('products.json');
  // Парсим данные из JSON формата в JS
  const productsArray = await response.json();
  console.log(productsArray);
  // Запускаем ф-ю рендера (отображения товаров)
  renderProducts(productsArray);
}

function renderProducts(productsArray) {
  productsArray.forEach(function (item) {
    const productHTML = `
    <li class="tabs__box" data-item="${item.category}">
    <article class="tabs__item card" data-id="${item.id}">
      <h3 class="card__title">${item.title}</h3>
      <a href="#" class="card__image">
        <picture>
          <source srcset="${item.imageUrl}" type="image/webp">
          <img loading="lazy" class="card__img" src="${item.imageUrlS}" width="171" alt="${item.title}">
        </picture>
      </a>
      <div class="card__box-price">
      <div class="card__price-new"><span>${item.priceNew}</span><span class="ruble">₽</span></div>
        <div class="card__price-old-box">
          <div class="card__price-old"><span>${item.priceOld}</span><span class="ruble">₽</span></div>
          <div class="card__volume"><span>за ${item.sizes} кг</span></div>
        </div>
      </div>
      <div class="card__box-button">
        <div class="card__counter-wrapper counter-wrapper">
          <button class="card__counter-control btn-reset" data-action="minus">-</button>
          <div class="card__counter-current" data-counter>1</div>
          <button class="card__counter-control btn-reset" data-action="plus">+</button>
        </div>
        <button class="card__btn btn-reset" type="button" data-cart>В корзину</button>
      </div>
    </article>
  </li>
    `;
    productsContainer.insertAdjacentHTML('beforeend', productHTML);
  });
}
